@import url('http://fonts.cdnfonts.com/css/adorable');

.login{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background: url('../../assets/Images/LoginBackground.avif');
    background-size: cover;
}

.login form{
    background-color: rgba(255,255,255,0.33);
    box-shadow: 0px 8px 32px 0px rgba(0,0,0,255,0.33);
    backdrop-filter: blur(3px);
    padding: 16px 16px 16px 16px;
    border-radius: 16px;
}




