.client-edit{
    color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-bottom: 30%;
    background: url('../../assets/Images/Forest_BG.avif');
    background-size: cover;
}
.client-new {
    color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: url('../../assets/Images/Forest_BG.avif');
    background-size: cover;
    padding-bottom: 50%;
}

.client-edit form {
    display: flex;
    justify-content: left;
    flex-direction: column;
    min-width: 400px;
    box-shadow: 0px 8px 32px 0px rgba(0, 0, 0, 0.33);
    padding: 30px;
    border-radius: 16px;
}

.client-new form {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    background-color: rgba(255, 255, 255, 0.33);
    box-shadow: 0px 8px 32px 0px rgba(0, 0, 0, 0.33);
    padding: 20px;
    border-radius: 16px;
    width: 80%;
    max-width: 800px;

}

.clientDetails {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-left: 20px;
    gap: 20px;
}

@media (max-width: 768px) {
    .clientDetails > * {
        flex-basis: 100%;
    }
}


.appointHistory {
    width: 1000px;
}
