.dashboard{
    color: #ffffff;
    width: 100%;
    height: 100%;
    padding-top: 50px;
    padding-bottom: 30%;
    background: url('../../assets/Images/Forest_BG.avif');
    background-size: cover;
}
.dashboardHeader h1{
    font-size: 30px;
    margin: 0;
    padding-left: 7.5%;
}
.calendarContainer {
    display: flex;
    justify-content: center;
    padding-left: 5%;
    padding-right: 5%;
    padding-bottom: 10px;
}
.customCalendar {
    margin: 10px;
}
.buttonAndTableContainer {
    display: flex;
    flex-direction: column;
    align-items: start;
    width: 90%;
    margin: 0 auto;
}
.buttonAndTableContainer button{
    margin: 10px
}
.dashBoardTable{
    width: 100%;
    height: 100%;
    padding: 10px;
    background: rgba(120, 120, 120, 0.485);
    box-shadow: 0 0 20px rgba(0,0,0,0.3);
}
